@import url(https://fonts.googleapis.com/css?family=Quicksand|Secular+One&display=swap|Roboto+Mono&display=swap);
body, html {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a.navbar-brand, a.active {
  color: #CBD5F5; }
  a.navbar-brand:hover, a.active:hover {
    color: #ff8064; }

a.active {
  margin-right: 50px;
  margin-top: 15px;
  padding-top: 35px; }
  a.active:hover {
    text-decoration: none;
    color: #ff8064; }

button.contact-btn {
  background-color: #172A45;
  padding: 5px 25px;
  display: inline-block;
  border: 1px solid #64FFDA;
  border-radius: 5px; }
  button.contact-btn:hover {
    background-color: none;
    border: 1px solid #ff8064; }
  button.contact-btn a {
    color: #64FFDA; }
    button.contact-btn a:hover {
      text-decoration: none;
      color: #ff8064; }
    @media (max-width: 767.98px) {
      button.contact-btn a {
        background-color: #172A45;
        padding: 0; } }
  @media (max-width: 767.98px) {
    button.contact-btn {
      background-color: #172A45;
      padding: 5px 7px; } }

.navigation {
  background-color: #172A45; }

/****** Header *****/
.homepage {
  background-color: #172A45; }

.header {
  height: 85vh;
  background-color: #172A45;
  position: relative;
  display: flex; }
  @media (max-width: 575.98px) {
    .header {
      height: 90vh; } }

.web-background {
  background-image: url(/static/media/dark_background.64c2702d.png);
  background-size: cover; }

.title-box {
  position: absolute;
  display: flex;
  height: 85vh;
  justify-content: center; }

.title {
  position: absolute;
  color: #CBD5F5;
  font-size: 6rem;
  font-family: 'Secular One', sans-serif;
  z-index: 1;
  line-height: 120px;
  margin-top: 4%; }
  @media (max-width: 575.98px) {
    .title {
      font-size: 5rem;
      line-height: 100px; } }

.title-caption {
  position: absolute;
  z-index: 1;
  margin-left: 5%;
  color: #64FFDA; }

.intro-box {
  display: flex;
  color: #CBD5F5;
  align-items: flex-end; }

.intro {
  font-size: 1.25rem;
  max-width: 500px; }
  @media (max-width: 575.98px) {
    .intro {
      font-size: .80rem;
      text-align: left;
      line-height: 25px; } }

/***** About Me *****/
.about {
  margin-top: 15%; }

.about-text {
  margin-left: 15%;
  padding-top: 80px; }
  @media (max-width: 767.98px) {
    .about-text {
      padding-top: 0;
      max-width: 100%;
      margin-left: 0%;
      margin-top: 10%;
      text-align: center; } }

/**** Experience *****/
.experience {
  margin-top: 15%; }
  .experience .tabs {
    margin-top: 5%; }
    .experience .tabs a.nav-link {
      color: #6375a8;
      border: none;
      text-align: center;
      cursor: pointer;
      padding: 10px 0;
      width: 200px;
      margin-right: 0; }
    .experience .tabs a.active.nav-link {
      color: #64FFDA;
      background-color: #334880;
      padding: 10px 0;
      margin: 0;
      text-align: center;
      margin-right: 0; }
    .experience .tabs .tab-pane h5 {
      color: #CBD5F5;
      margin-bottom: 0; }
    .experience .tabs .tab-pane .date {
      opacity: .9;
      font-size: .8rem; }
    .experience .tabs .tab-pane .asterik {
      margin-right: 10px; }
    @media (max-width: 767.98px) {
      .experience .tabs .nav {
        align-items: center;
        margin: 5% 0; } }

/*** Projects ****/
.projects {
  margin-top: 15%;
  /*.outline {
        border: 1px solid $code;
        position: absolute;
        width: 100%;
        height: 80%;
        top: 25%;
    }*/ }
  .projects .box {
    padding: 10%;
    position: relative;
    padding-bottom: 2%;
    background-color: #1D3152;
    margin-top: 8%;
    text-align: left;
    color: #CBD5F5; }
    .projects .box .info {
      color: #ABB3CE; }
    .projects .box .tech {
      color: #ABB3CE;
      padding-top: 20%;
      font-size: .8rem;
      word-spacing: 25px; }

/**** Footer ****/
footer {
  margin-top: 15%;
  padding-bottom: 2%; }
  footer a {
    font-family: "Secular One", sans-serif;
    color: #ABB3CE; }
    footer a:hover {
      text-decoration: none;
      color: #ABB3CE; }
  footer .box {
    background-color: #1D3152;
    text-align: left;
    padding: 5%;
    margin-bottom: 5%; }

.back {
  font-size: .9rem; }
  @media (max-width: 767.98px) {
    .back {
      text-align: center; } }

