html, body {
  font-family: 'Quicksand-Regular', sans-serif;
  scroll-behavior: smooth;
  font-size: 17px;
  padding: 0;
  color: #CBD5F5; }

p, a {
  font-family: 'Quicksand', sans-serif;
  line-height: 35px;
  color: #CBD5F5; }
  @media (max-width: 767.98px) {
    p, a {
      font-size: .88rem;
      line-height: 25px; } }

h1, h2, h3 {
  font-family: 'Secular One', sans-serif;
  color: #CBD5F5; }

.code {
  font-family: 'Roboto Mono', monospace;
  color: #64FFDA; }

.code-color {
  color: #64FFDA; }

.line {
  margin-left: 5px; }
  @media (max-width: 767.98px) {
    .line {
      display: none; } }

